.footer {
    background-color:$dark;
    margin-top:auto;
    padding: 1rem 0rem;
    p {
      text-align:center;
      color:#fff;
      font-size:.8rem;
    }
    ul {
      text-align:center;
      li {
        color:#fff;
        font-size: .8rem;
        a {
          color:#fff;
          font-size: .8rem;
          &:hover {
            text-decoration:none;
            box-shadow: 0 0.15rem $teal;
            transition: box-shadow .2s, color .2s;
          }
        }
        &:not(:last-child):after {
          content: '';
          display: inline-block;
          position: relative;
          margin-left:.8rem;
          top:.2rem;
          width: 1px;
          height: 1rem;
          background: rgba(255, 255, 255, 0.6);     
        }
      }
    }
}